.App {
  background: black;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.game {
  background: #333;
  position: relative;
}

.sprite {
  display: block;
  position: absolute;
  background: #eee;
}

.PLAYER {
  z-index: 1;

  &.dead {
    background: crimson;
  }
}

.MONSTER {
  background: green;

  &.dead {
    background: blue;
  }
}

.gamelog {
  background: white;
}
